/* Файл main-nav.less */
.main-nav__wrapper {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    margin: 0;
    justify-content: space-between;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  padding: 22px;
  width: 60px;
  height: 60px;
  box-sizing: border-box;
  outline: none;
  border: none;
  background-color: #fff;
}
.main-nav__toggle:active,
.main-nav__toggle:hover,
.main-nav__toggle:focus {
  background-color: #F0EBFF;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 22px;
  right: 22px;
  width: 16px;
  height: 2px;
  background: #333333;
  box-shadow: 0 7px 0 0 #333333, 0 14px 0 0 #333333;
  cursor: pointer;
  z-index: 2;
}
.main-nav--opened .main-nav__toggle::after,
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 29px;
  right: 22px;
  width: 16px;
  height: 2px;
  background: #333333;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    display: flex;
    flex-direction: column;
    min-height: 289px;
  }
  .main-nav--nojs .main-nav__wrapper .site-list {
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    z-index: 10;
  }
  .main-nav--nojs .site-list__item {
    display: flex;
    min-width: 320px;
    max-width: 767px;
  }
  .main-nav--opened .main-nav__site-list {
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    z-index: 10;
  }
}
